    import headTop from '@/components/base/header/head'
    export default {
        components: {
            headTop
        },
        data() {
            return {
                show: false,  // 判断筛选弹窗显示或隐藏
                //  筛选框选择
                shopName: ['未处理','已联系','已解决','跟进中','无法联系','未达成','其他'],
                company_name: "",  // 类型
                id: null,  //  订单id
                suggest: null,   //  订单留言
                time: null,   //   订单时间
                numId: null,   //  订单处理类型
            }
        },
        created(){
            this.suggest = this.$route.params.suggest;
            this.id = this.$route.params.id;
            this.time = this.$route.params.time;
        },
        methods:{
//            提交保存按钮
            submit(){
                this.show = true;
            },
            //            筛选弹窗确定按钮
            choice(){
                this.shopName.forEach((item,index)=>{
                    if(this.company_name == item){
                        this.numId = index
                    }
                });
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.suggest_deal,
                    type: "post",
                    data: {
                        id: this.id,
                        suggest_status: this.numId,
                    },
                    success: (response, allResponse) => {
                        this.$vux.toast.text(allResponse.msg, 'middle');
                        setTimeout(()=>{
                            this.$router.push('/manage/suggest');
                            this.show = false;
                        },900)
                    }
                });

            },
        }
    }



